<template>
  <div id="app">
    <div id="header">
      <header-component/>
    </div>

    <div id="content">
      <main-component/>
    </div>

    <div id="footer">
      <footer-component/>
    </div>
  </div>
</template>

<script>
// VueMeta
import HeaderComponent from './components/Header.vue'
import MainComponent from './components/Main.vue'
import FooterComponent from './components/Footer.vue'
import Vue from 'vue'
import Meta from 'vue-meta'
Vue.use(Meta)

export default {
  name: 'App',
  components: {
    'header-component': HeaderComponent,
    'main-component': MainComponent,
    'footer-component': FooterComponent
  },
  metaInfo: {
    title: 'Vue.js와 Spring Boot',
    titleTemplate: '%s | Vue.js와 Spring Boot를 이용한 사이트 개발',
    htmlAttrs: {
      lang: 'en-US'
    },
    meta: [
      { charset: 'utf-8' },
      { name: 'description', content: 'Vue.js와 Spring Boot를 이용한 사이트 개발' },
      { name: 'viewport', content: 'user-scalable=no, width=device-width, initial-scale=1' }
    ]
  }
}
</script>

<style>
  @import './assets/css/style.css'
</style>



